/*  GWOPA custom styles */

.contibutioncentered {
  width: 20%;
  text-align: center;
}

.buttonbudget {
  margin-top: 5px;
  float: right;
  background-color: #007dac;
  padding: 12px;
  font-size: 15px;
  color: white;
}

#formfield-form-widgets-startplanned,
#formfield-form-widgets-startactual,
#formfield-form-widgets-completionplanned,
#formfield-form-widgets-completionactual {
  padding-left: 30px;
}

select#formfield-form-widgets-members {
  height: 34px !important;
}

.plone-toolbar-expanded #edit-zone li:not(.active) a:hover>span {
  color: white;
}

#edit-zone nav > ul a:focus {
    color: white;
}

.plone-toolbar-top #edit-zone nav>ul {
  float: right;
}

.logo_img {
  height: 50px;
  top: 0;
  position: absolute;
  left: 10px;
}

.plone-toolbar-top #edit-zone nav {
  padding-left: 125px;
}

#edit-zone {
  background: white;
  border-bottom: 1px solid #f1f1f1;
  box-shadow: 0px 2px 6px 0px #cecece;
}

#edit-zone nav>ul a {
  color: #696969;
}

.btn_remove, .btn_remove:hover {
  color: #fff;
  background-color: #ac2925;
  border-color: #d43f3a;
  box-shadow: none;
  margin-bottom: 25px;
  padding: 10px 16px;
  font-size: 18px;
  line-height: 1.33;
  border-radius: 6px;

}
.btn_warning, .btn_warning:hover {
  color: #fff;
  background-color: #d58512;
  border-color: #985f0d;
  box-shadow: none;
  margin-bottom: 25px;
  padding: 10px 12px;
  font-size: 18px;
  line-height: 1.33;
  border-radius: 6px;
}

.portaltype-project .fullwidth {
    width: 100%;
}

.discussion .commentImage {
    width: 40px;
}


/* SITE STATE COLORS */
.actions-in-object {
  float:right;
}
span.icon-plone-contentmenu-workflow.label-state-private:before {
    color: #c4183c;
}
span.icon-plone-contentmenu-workflow.label-state-published:before {
    color: #007bb3;
}
span.icon-plone-contentmenu-workflow.label-state-pending:before {
    color: #e2e721;
}

/* SIDEBAR */

div.outer-wrapper {
  margin-left: 10%
}

#portal-logout {
  display: unset;
  padding-top: unset;
  float: unset;
}

#portal-logout {
  padding-left: 0px;
}

.sidenav {
  height: 100%;
  width: 10%;
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
  background-color: #004657;
  overflow-x: hidden;
}

.sidenav a {
  text-decoration: none;
  color: white;
  display: block;
  text-align: left;
}

.sidenav a:hover {
  color: #f1f1f1;
}

@media screen and (max-height: 450px) {
  .sidenav a {font-size: 18px;}
}

/* END SIDEBAR */

