.template-login_form div#content-core,
.template-logged_out div#content-core {
  padding-left: 40px;
  padding-right: 40px;
  background-color: white;
  width: 420px;
}

.template-login_form div#content-core,{
  margin-top: 120px;
}

.template-login_failed div#content-core {
    padding-left: 40px;
    padding-right: 40px;
    background-color: white;
    margin: 0 auto 6px;
}

body.template-login_form #content-core,
body.template-logged_out #content-core {
  max-width: 420px;
}

.template-login_failed #content-core, {
    width: 420px;
    padding-bottom: 20px;
    padding-top: 20px;
}

.template-login_form .documentDescription,
.template-login_failed .documentDescription {
    text-align: center;
    background-color: white;
}

#image_login {
  padding-bottom: 30px;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

#login-forgotten-password {
  padding-top: 10px;
}

ul.item {
  padding-left: 0px;
}

input#sso.context {
  width: 100%;
}

.template-login_failed .portalMessage.error {
  width: 420px;
  padding-left: 40px;
  padding-right: 40px;
  margin: 6px auto 6px;
}

.template-logged_out .documentDescription {
    background-color: #41C0F1;
    border: 1px solid #41C0F1;
    color: rgba(0, 0, 0, 0.5);
    width: 420px;
    padding-left: 40px;
    padding-right: 40px;
    margin: 6px auto 6px;
    height: 50px;
    padding-top: 11px;
    text-align: center;
}


